import { Component } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import { RegistrationService } from '../../services/registration.service';

import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css']
})
export class RegistrationComponent {
  title = 'app';
  users:any;
  name:string;
  password:string;

  usernamesCheck:any = [];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private registrationService: RegistrationService){
    this.registrationService.getUsers()
      .subscribe(users => {
        this.users = users;
      });
  }

  addUser(event) {
      event.preventDefault();
      let newUser = {
        name: this.name,
        password: this.password
      }
      console.log(this.users);
      this.extractNames(this.name);
      if(!this.usernamesCheck.includes(this.name)){
        this.registrationService.addUser(newUser)
          .subscribe(user => {
            this.users.push(user);
            this.name = '';
            this.password = '';
          });
          this.router.navigate(['/login']);
      } else {
        alert('Username already in use. Pick a new one!');
      }
      console.log(this.name);

  }

  extractNames(nme:string) {
    for(let i of this.users){
      let a = i['name'];
      console.log(a);
      this.usernamesCheck.push(a);
    }
  }

}



